.container{
    max-width: 100%;
    margin: 0 auto;
    padding: 15px;
    &.container_padding_null{
        padding: 0;
    }
    
}

.container-fluid{
    padding: 6px;
}


@media screen and (min-width: 576px) {
    .container{
        max-width: 540px;
    }
}

@media screen and (min-width: 768px) {
    .container{
        max-width: 720px;
    }
}

@media screen and (min-width: 992px) {
    .container{
        padding: 6px;
        max-width: 1100px;
        &.container_padding_null{
            padding: 0;
        }
    }
}

@media screen and (min-width: 1200px) {
    .container{
        max-width: 1280px;
    }
}

@media screen and (min-width: 1400px) {
    .container{
        max-width: 1320px;
    }
}

@media screen and (min-width: 1580px) {
    .container{
        max-width: 1582px;
    }
}