@import "../../styles/theme.scss";

.home{
    &__title{
        width: 100%;
        text-align: center;
        padding-bottom: 20px;
        font-size: 40px;
    }
    &__list{
        padding-top: 20px;
        padding-bottom: 20px;
    }

    &-post{
        display: flex;
        flex-wrap: wrap;
        gap: 42px;
        &__item{
            align-items: stretch;
            border-radius: 10px;
            width: 100%;
            padding: 10px;
            background-color: $write;
            -webkit-box-shadow: 0px 0px 8px 14px rgba(232, 170, 49, 0.19);
            -moz-box-shadow: 0px 0px 8px 14px rgba(232, 170, 49, 0.19);
            box-shadow: 0px 0px 8px 14px rgba(232, 170, 49, 0.19);
            @media screen and (min-width: 996px) {
                width: calc(50% - 28px);
            }
            @media screen and (min-width: 1200px) {
                width: calc(33.33% - 28px);
            }
            @media screen and (min-width: 1580px) {
                width: 22.9%;
            }
        }
    }
    &__empty{
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        z-index: 6;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 60px;
        font-weight: 700;
    }

    &__filter{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: relative;
        z-index: 8;
    }

    &_filter-active.btn{
        background-color: $primary_text;
        color: $write;
    }
}

.fade{
    &-enter {
        opacity: 0;
        transform: scale(0.9);
    }
    &-enter-active {
        opacity: 1;
        transform: translateX(0);
        transition: opacity 300ms, transform 300ms;
    }
    &-exit {
        opacity: 1;
    }

    &-exit-active {
        opacity: 0;
        transform: scale(0.9);
        transition: opacity 300ms, transform 300ms;
    }
    &-exit-done{
        display: none;
    }
}

.del{
    &-enter {
    }
    &-enter-active {
    }
    &-exit {
    }
    &-exit-active {
        opacity: 0;
        transition: opacity 700ms ease-out;
    }
}



