@import "../../styles/vars.scss";

.loader{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: $black;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 300;
    top: 0px;
    left: 0px;
    &__icon{
        width: 200px;
        height: 200px;
        &>svg{
            width: 100%;
            height: 100%;
        }
    }
}