@import '../../styles/vars.scss';

.post{
    position: relative;
    &__preview{
        object-fit: cover;
        object-position: center;
        width: 100%;
        height: 100%;
    }
    &__preview-container{
        width: 100%;
        height: 340px;
        position: relative;
    }
    &__title{
        padding-top: 20px;
        padding-bottom: 10px;  
        color: $black;
    }
    &__cat{
        font-size: 14px;
        font-weight: bold;
        color: $write;
    }
    &_cat-position-absolute{
        position: absolute;
        top: 0px;
        right: 0px;
        background-color: $primary_text;
        padding: 12px;
    }
    &__link{
        color: $black;
        font-weight: bold;
        transition: color 400 ease-in-out;
        &:hover{
            color: $primary_text;
            transition: color 400 ease-in-out;
        }
    }
    &__navigation{
        position: absolute;
        left: 0px;
        bottom: 0px;
    }

    &_padding-navigation{
        padding-bottom: 46px;
        height: 100%;
    }

    &-navigation{
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 46px;
        width: 100%;
    }

    &__remove{
        position: absolute;
        left: 10px;
        top: 10px;
    }

    &-remove{
        padding: 6px;
        width: 40px;
        height: 40px;
        background-color: transparent;
        border: none;
        cursor: pointer;
        &__icon{
            width: 40px;
            height: 40px;
            fill: $write;
        }
    }
}
