@import "../../../styles/vars.scss";

.button{
    width: 40px;
    height: 40px;
    background-color: transparent;
} 

.button-favorite{
    fill: $black_opacity;
    transform: scale(0.8);
    cursor: pointer;
    transition: fill 300ms ease, transform 300ms ease;
    border: none;
    &:hover{
        transition: fill 300ms ease, transform 300ms ease;
        transform: scale(1);
    }
    &>svg{
        width: 100%;
        height: 100%;
    }
    &.button-favorite_active{
        fill: $primary_text;
    }
}
